import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ArticlesService } from 'src/app/services/uiServices/articles.service';
import { DomSanitizer } from '@angular/platform-browser';
import { NavigationService } from 'src/app/services/navigation.service';

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class ArticlesComponent implements OnInit {

  articles: Array<Article>;
  constructor(private articlesService: ArticlesService, private sanitizer: DomSanitizer, private navigationService: NavigationService
  ) { }

  ngOnInit() {
    this.loadArticles();
    this.navigationService.setNavigationLink = "articles";
  }
  async loadArticles() {
    this.articles = await this.articlesService.getArticlesList();
    this.articles = this.articles.filter((article) => article.isShowen);
    this.articles = this.articles.map((article) => {
      let newArticle: any = article;
      newArticle.article = this.sanitizer.bypassSecurityTrustHtml(article.article);
      return newArticle;
    });
  }
}
