import { Component, OnInit, OnDestroy } from "@angular/core";
import { ScheduleService } from "src/app/services/uiServices/schedule.service";
declare var $: any;
declare var mixitup: any;
import * as moment from 'moment';
var mixer: any;

@Component({
  selector: "app-programs-schedule",
  templateUrl: "./programs-schedule.component.html",
  styleUrls: ["./programs-schedule.component.css"]
})
export class ProgramsScheduleComponent implements OnInit, OnDestroy {

  weekSchedule: WeekSchedule = {
    sat: [],
    sun: [],
    mon: [],
    tue: [],
    wed: [],
    thu: [],
    fri: []
  };
  constructor(private scheduleService: ScheduleService) {

  }

  ngOnInit() {
    this.getWeekPrograms();


  }
  async getWeekPrograms() {
    var startOfWeek = moment();
    var endOfWeek = startOfWeek.clone().add(6, 'd');

    var days = [];
    var day = startOfWeek;

    while (day <= endOfWeek) {
      days.push(day.toDate());
      day = day.clone().add(1, 'd');
    }
    console.log("Whole week");
    console.log(days.toString());
    let sat = days.find(day => day.toString().substr(0, 3) == "Sat");
    console.log(sat);
    let sun = days.find(day => day.toString().substr(0, 3) == "Sun");
    console.log(sun);
    let mon = days.find(day => day.toString().substr(0, 3) == "Mon");
    console.log(mon);
    let tue = days.find(day => day.toString().substr(0, 3) == "Tue");
    console.log(tue);
    let wed = days.find(day => day.toString().substr(0, 3) == "Wed");
    console.log(wed);
    let thu = days.find(day => day.toString().substr(0, 3) == "Thu");
    console.log(thu);
    let fri = days.find(day => day.toString().substr(0, 3) == "Fri");
    console.log(fri);

    this.weekSchedule = {
      sat: await this.scheduleService.getDaySchedules(sat.toString().substr(0, 3), moment(sat).format('YYYY-MM-DD')),
      sun: await this.scheduleService.getDaySchedules(sun.toString().substr(0, 3), moment(sun).format('YYYY-MM-DD')),
      mon: await this.scheduleService.getDaySchedules(mon.toString().substr(0, 3), moment(mon).format('YYYY-MM-DD')),
      tue: await this.scheduleService.getDaySchedules(tue.toString().substr(0, 3), moment(tue).format('YYYY-MM-DD')),
      wed: await this.scheduleService.getDaySchedules(wed.toString().substr(0, 3), moment(wed).format('YYYY-MM-DD')),
      thu: await this.scheduleService.getDaySchedules(thu.toString().substr(0, 3), moment(thu).format('YYYY-MM-DD')),
      fri: await this.scheduleService.getDaySchedules(fri.toString().substr(0, 3), moment(fri).format('YYYY-MM-DD')),
    };
    console.log(this.weekSchedule);
    $(document).ready(function () {
      mixer = mixitup('#Container', {
        load: {
          filter: 'none',

        }
      });

    });

  }
  getPublishTime(time: string) {
    return time.substr(time.indexOf("T") + 1, 5);
  }
  ngOnDestroy() {
    mixer.destroy();
  }

}
