import { Component, OnInit } from '@angular/core';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { ActivatedRoute } from '@angular/router';
import { ServiceHandlerProvider } from 'src/app/services/service-handler.service';
import { Constants } from 'src/app/Constants';

@Component({
  selector: 'app-program-details',
  templateUrl: './program-details.component.html',
  styleUrls: ['./program-details.component.css']
})
export class ProgramDetailsComponent implements OnInit {
  selectedProgram: Program = {
    name: "",
    description: "",
    duration: "",
    programDate: "",
    icon: "",
    mobBanner: "",
    playlistUrl: "",
    webBanner: "",
    isShowen: false,
    presenter: ""
  };
  id = 'OH3_er5i4IQ';
  programYoutubeVideos: Array<any>;

  programId: string;
  constructor(
    private programService: ProgramsService,
    private route: ActivatedRoute,
    private serviceHandeler: ServiceHandlerProvider
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.programId = params.id;
      console.log(this.programId);
      this.getProgramDetails();
    });
  }
  async getProgramDetails() {
    this.selectedProgram = await this.programService.getProgramsDetails(this.programId);
    let params = { "part": "snippet,contentDetails", "maxResults": "50", "playlistId": this.selectedProgram.playlistUrl, "key": Constants.YOUTUBE_API_KEY };
    this.serviceHandeler.runService("https://www.googleapis.com/youtube/v3/playlistItems", "GET", null, null, null, params).subscribe(response => {
      console.log(response);
      this.programYoutubeVideos = response.items;
    }, error => {
      console.log(error);
    })
  }
}
