import { Injectable } from '@angular/core';
import { ServiceHandlerProvider } from '../service-handler.service';
import { Constants } from 'src/app/Constants';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ScheduleService {

  constructor(private serviceHadler: ServiceHandlerProvider) { }

  async getNowAndUpcomingPrograms() {

  }
  async getDaySchedules(dayName: string, dayDate: string): Promise<Array<Schedule>> {
    console.log(dayName + "---" + dayDate);
    return new Promise<Array<Schedule>>((resolve, reject) => {
      let query = `query {
        daySchedules(day:"${dayName}",date:"${dayDate}"){
          id
          timeDate
          type
          program{
            id,
            name,
            icon
          }
          event{
            id,
            name,
            icon
          }
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.daySchedules) {
          let daySchedules: Array<Schedule> = response.data.daySchedules;
          daySchedules = daySchedules.filter(item => item.program != null);
          resolve(daySchedules);
        }
        else {
          reject("Failed to load daySchedules");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
}
